.footer {
    height: 100px;
    background-color: rgba(240, 240, 240, 0);
    padding: 16px 50px 16px 50px;
}

.footer-item {
    margin-left: 20px;
}

.footer-item a {
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
    font-size: 13px;
    color: #586069;
}

.footer-item a:hover {
    color: #0366d6;
    text-decoration: underline;
}
