.register-form {
    height: 780px;
    width: 450px;
    float: left;
    margin-left: 75px;
    margin-right: 0;
    margin-top: 75px;
    background-color: #fff;
    border-radius: 3px;
}

.register-form-wrapper {
    height: 100%;
    font-size: 12px;
    color: #6a737d;
    line-height: 1.5;
    margin: 24px;
}

.register-form p {
    font-size: 12px;
    color: #6a737d;
}

.title {
    width: 333px;
    line-height: 2.5;
    height: 23px;
    color: #586069;
    font-size: 13px;
    margin: 0 0 0 0;
}

.input {
    width: 100%;
    height: 44px;
    border-radius: 5px;
    border: 1px solid #d1d5da;
    box-shadow: inset 0 1px 2px rgba(27, 31, 35, 0.075);
    font-size: 14px;
    margin-top: 5px;
}

.term {
    font-size: 12px;
    color: #6a737d;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
    line-height: 1.5;
    text-align: center;
}

.register-button {
    background-color: #2ebc4f;
    font-weight: 500;
    color: #fff;
    border: 0;
    font-size: 16px;
    width: 100%;
    text-align: center;
    height: 60px;
    border-radius: 3px;
    cursor: pointer;
    transition: background-color 0.2s linear;
}

.register-button:hover {
    background-color: rgb(78, 207, 107);
    color: #fff;
}
